.banner-title {
  font-weight: bold;
}

.banner-icon-bar {
  display:flex;
  flex-direction: row;
  margin-top: 8px;
  margin-bottom: 8px;
  justify-content: space-between;
}

.banner-right-icon-bar {
  display:flex;
  flex-direction: row;
  margin-top: 8px;
  margin-bottom: 8px;
}

.banner-left-icon-bar {
  display:flex;
  flex-direction: row;
  margin-top: 8px;
  margin-bottom: 4px;
}

.banner-icon {
  height: 32px;
  width: 32px;
  margin-left: 16px;
  cursor: pointer;
}

.banner-show-help {
  display: inline;
}

.banner-hide-help {
  display: none;
}

.banner-help-content {
  background-color: var(--notification-info);
  position: absolute;
  top: 16px;
  left: 110px;
  padding: 16px;
}

.banner-label {
  margin-top: 4px;
  margin-left: 16px;
  cursor: pointer;
}

.banner-hr {
  margin-left: 20px;
  border-top: 1px solid var(--blue-02);
}

.banner-more-games-modal {
  padding: 10px;;
  background-color: white;
  position: absolute;
  top: 55px;
  left: 20px;
  border: 1px solid blue;
}

.banner-profile-selection {
  display: flex;
  align-items: center;
}

.banner-profile-selection-title {
  padding-left: 5px;
  color: var(--blue-03);
}

.banner-profile-selection-select {
  color: var(--blue-03);
  padding: 3px;
  border-color: var(--blue-03);
}
